import React from "react";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";

import { FaLinkedin, FaGithub } from "react-icons/fa";

import { OutboundLink } from "gatsby-plugin-google-analytics";

const useStyles = makeStyles(theme => ({
  footer: {
    backgroundColor: "#fff",
    padding: theme.spacing(6)
  },
  link: {
    hover: "fff"
  }
}));

const Footer = () => {
  const classes = useStyles();

  return (
    <footer
      className={classes.footer}
      style={{ borderTop: "1px solid #010A26" }}
    >
      <Typography variant="h6" align="center" gutterBottom color="secondary">
        Contact
      </Typography>
      <Typography
        variant="subtitle1"
        align="center"
        color="secondary"
        component="p"
      >
        fredrik96.johansson@gmail.com
      </Typography>
      <Typography
        variant="subtitle1"
        align="center"
        color="secondary"
        component="p"
      >
        <OutboundLink
          rel="noopener noreferrer"
          target="_blank"
          href="https://github.com/FredrikErikJohansson"
          style={{ color: "inherit" }}
        >
          <FaGithub size="30px" style={{ margin: "12px" }} />
        </OutboundLink>
        <OutboundLink
          rel="noopener noreferrer"
          target="_blank"
          href="https://www.linkedin.com/in/fredrik-johansson-66364a179/"
          style={{ color: "inherit" }}
        >
          <FaLinkedin size="30px" style={{ margin: "12px" }} />
        </OutboundLink>
      </Typography>
    </footer>
  );
};

export default Footer;
